@import "/Users/Jen/Desktop/wolverine/saucony/sass/elements/_variables.scss";

.social-icons {
    list-style:none;
    display: flex;
    flex-direction:row;
    justify-content:space-between;
    width: 70%;
    margin:5vh auto;
    padding: 5vh 0;
    li {
        font-size: 3rem;
        a {
            text-decoration:none;
            font-family: 'gotham black';
            color:rgba(80, 210, 197, 0.834);
        }
    }
}

.meta {
    margin: 4vh auto;
    width: 600px;
    h3 {
        text-align:center;
        padding-top: 15vh;
        text-transform:uppercase;
        font-family: 'gotham black';
        color:rgba(80, 210, 197, 0.834);
    }
    .meta-links-list {
        list-style:none;
        display: flex;
        flex-direction: row;
        align-items:center;
        justify-content: space-evenly;
        li > a {
            text-decoration: none;
            color:rgba(80, 210, 197, 0.834);
            font-family: 'gotham bold';
            font-size: 14px;
            text-transform: uppercase;
            border: 2px solid rgba(80, 210, 197, 0.344);
            padding: 5px 15px;
        }
    }
}

body > footer > div {
    width:600px;
    overflow: hidden;
    img {
        width: 1000px;
        height: auto;
    }
}

